<template>
  <div class="page">
    <van-nav-bar
      title="注册账号"
      @click-left="onClickLeft"
    >
      <img
        slot="left"
        src="../assets/images/loginRegistered/arrow.png"
        alt=""
        width="33px"
        height="16.3px"
        style="vertical-align: middle;"
      >
    </van-nav-bar>
    <div class="logo">
      <h1>亿发<span>商城</span></h1>
      <div></div>
    </div>
    <div class="form">
      <van-field
        v-model="numbering"
        placeholder="请选择商户编号"
        @focus="onNumbering"
      >
        <img
          class="icon"
          width="21px"
          height="17px"
          slot="left-icon"
          src="../assets/images/loginRegistered/numbering.png"
          alt=""
        >
      </van-field>
      <van-field
        v-model="phone"
        placeholder="请输入手机号"
        type="number"
      >
        <img
          class="icon"
          width="21px"
          height="17px"
          slot="left-icon"
          src="../assets/images/loginRegistered/phone.png"
          alt=""
        >
        <van-button
          class="verificationCode"
          slot="button"
          round
          size="small"
          @click="changeCode"
        >{{code}}</van-button>
      </van-field>
      <van-field
        v-model="verification"
        placeholder="请输入短信验证码"
        type="number"
      >
        <img
          class="icon"
          width="21px"
          height="17px"
          slot="left-icon"
          src="../assets/images/loginRegistered/verification.png"
          alt=""
        >
      </van-field>
      <van-field
        v-model="password"
        placeholder="输入新的登录密码"
        :type="showPassword?typeText:typePassword"
      >
        <img
          class="icon"
          width="21px"
          height="17px"
          slot="left-icon"
          src="../assets/images/loginRegistered/password.png"
          alt=""
        >
        <img
          class="icon"
          width="21px"
          height="17px"
          slot="right-icon"
          alt=""
          :src="showPassword?showPass.passwordShow:showPass.passwordHide"
          @click="onShow"
        >
      </van-field>
      <van-button
        round
        type="info"
        size="large"
        class="register"
      >立即注册</van-button>
      <div class="other">
        <van-checkbox
          v-model="checked"
          shape="square"
        >我同意《服务条款》和《隐私相关政策》</van-checkbox>
      </div>
      <van-popup
        v-model="numberingShow"
        position="bottom"
        :overlay="true"
      >
        <van-picker
          show-toolbar
          :columns="columns"
          @cancel="onCancel"
          @confirm="onConfirm"
        />
      </van-popup>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      numbering: "", //商户编号
      numberingShow: false,
      columns: ["杭州", "宁波", "温州", "嘉兴", "湖州"],
      phone: "", //手机号
      verification: "", //验证码
      code: "获取验证码",
      password: "",
      typePassword: "password",
      typeText: "text",
      showPassword: false,
      showPass: {
        passwordShow: "/static/image/show.png",
        passwordHide: "/static/image/hide.png"
      },
      checked: true
    };
  },
  methods: {
    onClickLeft() {
      this.$router.go(-1);
    },
    onNumbering() {
      this.numberingShow = true;
    },
    onConfirm(value, index) {
      this.numberingShow = false;
      this.numbering = value;
      console.log(`当前值：${value}, 当前索引：${index}`);
    },
    onCancel() {
      this.numberingShow = false;
      console.log("取消");
    },
    // 验证码倒计时
    changeCode() {
      let countdown = 60; //初始值
      if (countdown == 0) {
        this.code = "获取验证码";
      } else {
        let time = setInterval(() => {
          countdown--;
          this.code = countdown;
          if (countdown <= 0) {
            clearInterval(time);
            this.code = "获取验证码";
          }
        }, 1000);
      }
    },
    // 切换眼睛
    onShow() {
      this.showPassword = !this.showPassword;
    }
  }
};
</script>

<style>
.other .van-checkbox .van-checkbox__icon,
.other .van-checkbox .van-checkbox__icon .van-icon-success{
  width: 10px;
  height: 10px;
  border-radius: 2px;
  line-height: 8px;
}
.van-icon-success:before {
  margin-left: -2px;
}
</style>
<style lang="less" scoped>
.page {
  .logo {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 1.17rem;
    color: #0055ff;
    font-size: 18px;
    line-height: 0.96rem;
    h1 {
      span {
        color: #000000;
        font-weight: 300;
      }
    }
    div {
      width: 1.07rem;
      height: 0.11rem;
      margin: 0.81rem 0 1.02rem;
      background-color: #0055ff;
      border-radius: 2px;
    }
  }
  .form {
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 0 1rem;
    .icon {
      vertical-align: middle;
    }
    .verificationCode {
      width: 1.92rem;
      height: 0.6rem;
      border-radius: 10px;
      border: solid 1px #0055ff;
      color: #0055ff;
      line-height: 0.6rem;
    }
    .van-field {
      margin-top: 0.67rem;
      border-bottom: solid 1px #a0a0a0;
      .van-field__left-icon {
        margin-right: 0.35rem;
      }
    }
    .register {
      width: 7.99rem;
      height: 0.93rem;
      margin-top: 1.2rem;
      background-color: #0055ff;
      box-shadow: 0 0.03rem 0.21rem 0 rgba(0, 85, 255, 0.41);
      border-radius: 18px;
      font-size: 15px;
      line-height: 0.93rem;
    }
    .other {
      margin-top: .51rem;
    }
  }
}
</style>


